#animate {
  text-decoration: none;
  padding-right: 2em;
  position: relative;
  transition: all ease-in-out 250ms;
}
#animate::after {
  content: "";
  position: absolute;
  display: block;
  height: 0.4em;
  width: 0%;
  bottom: -1em;
  transition: all ease-in-out 250ms;
}

.custom-shape-divider-top-1626500503 {
  position: block;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1626500503 svg {
  position: relative;
  display: block;
  width: calc(153% + 1.3px);
  height: 67px;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.custom-shape-divider-top-1626500503 .shape-fill {
  fill: #273036;
}

